@font-face {
  font-family: "MetropolisMedium";
  src: url("../../../../public/assets/metropolis.medium.otf") format("opentype");
  /* You may include additional font formats (e.g., woff, woff2) for better browser compatibility */
}
@import url("https://fonts.googleapis.com/css2?family=Playfair+Display&family=Poppins:wght@300;400;500;600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@400;700&display=swap");

.product-detail-container {
  display: flex;
  gap: 40px;
  margin: 40px;
  margin-top: 60px;
  color: #324d67;
}

.product-detail-container h1 {
  font-size: 36px !important;
  font-weight: 800 !important;
}
.product-detail-container h2 {
  font-size: 40px !important;
  font-weight: 800 !important;
}
.product-detail-container h4 {
  font-size: 16px !important;
  font-weight: 700 !important;
}
.product-detail-container p {
  font-size: 16px !important;
}

.product-detail-image {
  border-radius: 15px;
  background-color: #ebebeb;
  object-fit: cover; /* Ensure the image covers the container while maintaining aspect ratio */
  object-position: center center; /* Center the image within the container */
  width: 400px;
  /* height: 400px; */
  cursor: pointer;
  transition: 0.3s ease-in-out;
}
.product-detail-image:hover {
  background-color: #f02d34;
}
.small-images-container {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-top: 20px;
}
.small-image {
  border-radius: 8px;
  background-color: #ebebeb;
  width: 90px;
  /* height: 70px; */
  cursor: pointer;
}

.selected-image {
  background-color: #f02d34;
}
.reviews {
  color: #f02d34;
  margin-top: 10px;
  display: flex;
  gap: 5px;
  align-items: center;
}

.product-detail-desc h4 {
  margin-top: 10px;
}

.product-detail-desc p {
  margin-top: 10px;
}
.average-rating {
  margin-left: 5px;
}
.rating-reviews {
  display: flex;
  align-items: center;
}
.reviews p {
  color: #324d67;
  margin-top: 0px;
}
.product-detail-desc .price {
  font-weight: 700;
  font-size: 26px;
  margin-top: 30px;
  color: #f02d34;
}
.price .old-price,
.product-price .old-price,
.price .old-price {
  color: gray;
  text-decoration: line-through;
}
.product-detail-desc .quantity {
  display: flex;
  gap: 20px;
  margin-top: 10px;
  align-items: center;
}
.product-detail-desc .buttons {
  display: flex;
  gap: 30px;
}
.buttons .add-to-cart {
  padding: 10px 20px;
  border: 1px solid #f02d34;
  margin-top: 40px;
  font-size: 18px;
  font-weight: 500;
  background-color: white;
  color: #f02d34;
  cursor: pointer;
  width: 200px;
  transform: scale(1, 1);
  transition: transform 0.5s ease;
}
.buttons .add-to-cart:hover {
  transform: scale(1.1, 1.1);
}

.buttons .shareBtn {
  background-color: #4caf50; /* Green background */
  color: white; /* White text */
  font-size: 16px; /* Font size */
  padding: 10px 20px; /* Padding for the button */
  border: none; /* Remove default border */
  border-radius: 5px; /* Rounded corners */
  font-size: 18px;
  font-weight: 500;
  margin-top: 20px;
  cursor: pointer;
  /* width: 200px; */
  transform: scale(1, 1);
  transition: background-color 0.3s ease, transform 0.3s ease; /* Smooth transition for hover effects */
  width: 434px;
}

/* Add this to your CSS styles */
.strikethrough {
  text-decoration: line-through;
  color: #a09391;
  font-size: 14px;
  font-weight: 700;
  margin-left: 5px;
}

.shareBtn:hover {
  background-color: #45a049; /* Darker green on hover */
  transform: translateY(-2px); /* Slight lift on hover */
}


.buttons .buy-now {
  width: 200px;

  padding: 10px 20px;
  background-color: #f02d34;
  color: white;
  border: none;
  margin-top: 40px;
  font-size: 18px;
  font-weight: 500;
  cursor: pointer;
  transform: scale(1, 1);
  transition: transform 0.5s ease;
}
.buttons .buy-now:hover {
  transform: scale(1.1, 1.1);
}
.quantity-desc {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid gray;
}
.quantity-desc span {
  font-size: 16px;
  padding: 6px 12px;
  cursor: pointer;
}
.quantity-desc .minus {
  color: #f02d34;
}
.quantity-desc .num {
  border-left: 1px solid gray;
  border-right: 1px solid gray;
  font-size: 20px;
}
.quantity-desc .plus {
  color: rgb(49, 168, 49);
}

@media screen and (max-width: 800px) {
  .product-detail-desc .buttons {
    display: flex;
    gap: 15px;
  }
  .buttons .buy-now {
    width: 50%;
  }
  .buttons .add-to-cart {
    width: 50%;
  }
  .product-detail-container {
    margin: 20px;
  }
  .buttons .shareBtn {
    width: 100%;
  }
}

@media screen and (max-width: 470px) {
  .rating-badge {
    position: absolute;
    display: flex;
    justify-content: center;
    gap: 5px;
    align-items: center;
    bottom: 5px !important;
    left: 0px !important;
    background-color: red;
    color: white;
    padding: 5px;
    border-radius: 3px;
    font-weight: bold;
    font-size: 10px !important;
    z-index: 1;
  }
  .discount-box {
    position: absolute;
    bottom: 5px !important;
    right: 0px !important;
    background-color: red;
    color: white;
    padding: 5px;
    border-radius: 3px;
    font-weight: bold;
    font-size: 12px;
    z-index: 1;
  }
}
