.mainFlex {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #fff;
}

.biggest {
  width: 65%; /* Adjust the width as needed */
}

.login-logo {
  margin: 10px 20px;
}

/* Start Navigation here */
.nav {
  background-color: #d0e1f9;
  min-width: 160px;
  min-height: 470px;
  float: left;
  position: relative;
  padding: 0px;
  text-align: center;
  border-radius: 10px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.26);
}

.nav ul {
  margin: 0;
  padding: 0;
}

.nav ul li {
  list-style: none;
  padding: 20px 0px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 20px;
  border-radius: 5px;
  margin: 10px 0;
  transition: background-color 0.3s;
}

.nav ul li:hover {
  background-color: #e0e0e0;
}

.nav ul li a {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: #75757d;
  font-weight: 400;
  font-size: 14px;
  width: 100%;
  justify-content: space-between;
  font-weight: 600;
}

.nav ul li a i {
  margin-right: 10px;
}

hr {
  padding: 25px 5px;
  background-color: #031dd2;
  position: absolute;
  display: block;
  top: 26%;
  left: 0px;
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
}

.nav ul .move2:hover ~ hr {
  top: 42%;
}

.nav ul .move3:hover ~ hr {
  top: 58%;
}

.nav ul .move4:hover ~ hr {
  top: 72%;
}

.fa {
  color: #bebec0;
}
/* Start Navigation here */

/* Start Introduction */
.intro {
  background-color: #1a33d5;
  min-height: 520px;
  min-width: 500px;
  position: absolute;
  top: -30px;
  left: 153px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: -10px 2px 10px rgba(0, 0, 0, 0.16);
  z-index: 999;
}
.intro .par {
  text-align: start;
}
.intro .par h3 {
  color: #fff;
  font-size: 36px;
  font-family: "Pacifico", cursive; /* Using the Pacifico font */
  text-shadow: 0 2px 30px rgba(0, 0, 0, 0.4);
  margin-bottom: 15px;
}

.intro .par span {
  color: #fff;
  font-size: 20px;
  font-weight: 300;
  opacity: 0.9;
  line-height: 30px;
  font-family: "Open Sans", sans-serif;
}

.intro .par .social {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 150px;
  color: #fff;
}
.intro .par .social .fa {
  color: #fff;
}
.intro .par .social a {
  margin-right: 15px;
}
/* End Introduction */

/* Start The Forms */

.login-container {
  position: relative;
  left: 654px;
}
.logged {
  display: flex;
  align-items: center;
  justify-content: center;
}
.login-container .login {
  position: absolute;
  top: 0;
  left: 0;
  background-color: #d0e1f9;
  min-width: 160px;
  min-height: 470px;
  border-radius: 0px 10px 10px 0px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.26);
  padding: 40px;
  padding-top: 80px;
}
.login-container .login p {
  color: #75757d;
  font-size: 13px;
  font-weight: 500;
  line-height: 20px;
}
.login-container .login p span a,
.login-container .login p span {
  color: #1a33d5;
  font-weight: 900;
  font-size: 14px;
  text-decoration: none;
}
.login-container .login input {
  display: block;
  margin-top: 20px;
  padding: 13px 15px;
  width: 275px;
  color: #bebec0;
  outline: none;
  transition: 0.6s linear;
  color: #757575;
  border: 0;
  box-shadow: -9px 7px 17px #ddd;
  transition: 0.6s ease-in-out;
}
.login-container .login input:focus {
  border: 2px solid #4259dd;
}

