.regopen {
  left: 0 !important;
}
.submit-button {
  border-radius: 5px;
  margin: 15px auto;
  background-color: #1a33d5;
  color: #fff;
  padding: 10px 20px;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.submit-button:hover {
  background-color: #0c1f7d;
}

.submit-button span {
  margin-left: 10px;
}

.password-input-container {
  position: relative;
}

.eye-button {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
}

