
.success-wrapper,
.cancel-wrapper {
  background-color: white;
  min-height: 60vh;
}
.success,
.cancel {
  width: 1000px;
  margin: auto;
  margin-top: 160px;
  background-color: #dcdcdc;
  padding: 50px;
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.success .icon {
  color: green;
  font-size: 40px;
}
.success h2 {
  text-transform: capitalize;
  margin-top: 15px 0px;
  font-weight: 900;
  font-size: 40px;
  color: #324d67;
}
.success h3 {
  text-transform: capitalize;
  margin-top: 15px 0px;
  font-weight: 900;
  font-size: 30px;
  color: #326760;
}
.success .email-msg {
  font-size: 16px;
  font-weight: 600;
  text-align: center;
}
.cancel p {
  font-size: 20px;
  font-weight: 600;
}
.success .description {
  font-size: 16px;
  font-weight: 600;
  text-align: center;
  margin: 10px;
  margin-top: 30px;
}
.success .description .email {
  margin-left: 5px;
  color: #f02d34;
}

.btn {
    width: 100%;
    max-width: 400px;
    padding: 10px 12px;
    border-radius: 15px;
    border: none;
    font-size: 20px;
    margin-top: 10px;
    margin-top: 40px;
    text-transform: uppercase;
    background-color: #f02d34;
    color: #fff;
    cursor: pointer;
    transform: scale(1, 1);
    transition: transform 0.5s ease;
  }
  .btn:hover {
    transform: scale(1.1, 1.1);
  }



  

/* about us start from here  */
.about-us-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.story-section,
.passion-section,
.wide-range-section {
  margin-bottom: 40px;
}

.about-us-container h2 {
  color: #004080;
  border-bottom: 2px solid #004080;
  padding-bottom: 10px;
  margin-bottom: 20px;
}

.about-us-container p {
  font-size: 16px;
  line-height: 1.5;
}

.bouquets-section {
  margin-top: 20px;
}

.about-us-container h3 {
  color: #008080;
  margin-bottom: 10px;
}
