
/* News Letter Below Banners  */

#newsletter .form {
    display: flex;
    width: 40%;
  }
  
  #newsletter {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
    background-image: url("b14.png");
    border-bottom: 0.75px solid #2e2e2e;
  
    background-color: #000;
    /* background-repeat: no-repeat; */
    background-position: 20% 30%;
  }
  
  #newsletter h4 {
    font-size: 22px;
    font-weight: 700;
    color: #fff;
    font-family: "Times New Roman", Times, serif;
  }
  
  #newsletter p {
    font-size: 14px;
    font-weight: 600;
    color: #818ea0;
    margin-top: 10px;
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  }
  
  #newsletter p span {
    color: #ffbd27;
  }
  
  #newsletter input {
    height: 3.125rem;
    padding: 0 1.25em;
    font-size: 14px;
    width: 100%;
    border: 1px solid transparent;
    border-radius: 4px;
    outline: none;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
  }
  
  #newsletter button {
    background-color: #088178;
    color: #fff;
    white-space: nowrap;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
  }
  
  button.normal {
    font-size: 14px;
    font-weight: 600;
    padding: 15px 30px;
    color: #000;
    background-color: #fff;
    border-radius: 4px;
    cursor: pointer;
    border: none;
    outline: none;
    transition: 0.2s;
  }
  
  .footer-logo {
    display: flex;
    position: relative;
    align-items: center;
    justify-content: center;
    height: 60px;
  }
  .footer-p {
    color: white;
    text-align: center;
    padding: 40px 0;
  }
  /* Add this CSS to your existing styles or create a new stylesheet */
  
  footer {
    background-color: #000;
    /* padding: 20px 0; */
  }
  
  .footer-logo img {
    width: 220px;
    height: 60px;
    padding-bottom: 10px;
  }
  
  .footer-container {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    margin-top: 20px;
    border-bottom: 0.75px solid #2e2e2e;
    border-top: 0.75px solid #2e2e2e;
  }
  
  .footer-section {
    flex: 1;
    padding: 0 20px;
  }
  
  .footer-container h4 {
    font-size: 1.5rem;
    color: #fff;
    margin-bottom: 20px;
    font-size: 16px;
  }
  
  .footer-container ul {
    list-style: none;
    padding: 0;
  }
  
  .footer-container ul li {
    margin-bottom: 8px;
  }
  
  .footer-container ul li a {
    text-decoration: none;
    color: #aeaeae;
    text-transform: uppercase;
    font-size: 12px;
    /* font-family: metropolis-regular; */
    line-height: 12px;
  }
  
  .footer-container ul li a:hover {
    color: #ff6b35; /* Adjust hover color as needed */
  }
  
  .social-icons {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    margin: 10px 0 0 0;
    border-bottom: 0.75px solid #2e2e2e;
  }
  .social-icons ul {
    margin: 10px 0;
    display: flex;
    gap: 15px;
  }
  .social-icons li {
    display: flex;
    text-decoration: none;
    list-style: none;
  }
  .social-icons li:hover {
    color: #ef3636;
  }
  .social-icons svg {
    font-size: 22px;
  }
  
  /* Responsive Styling */
  @media screen and (max-width: 768px) {
    .footer-container {
      flex-direction: column;
      align-items: center;
    }
  
    .footer-section {
      width: 100%;
      margin-bottom: 20px;
    }
  
    .footer-logo img {
      margin-bottom: 20px;
    }
  }
  