.hero-banner-container {
  width: 100% !important;
  max-width: 100vw !important;
  overflow: hidden;
  position: relative;
  /* padding-top: 60px; */
}

.hero-banner-container .beats-solo {
  font-size: 20px;
}
.hero-banner-container button {
  border-radius: 15px;
  padding: 10px 16px;
  background-color: #f02d34;
  color: white;
  border: none;
  margin-top: 40px;
  font-size: 18px;
  font-weight: 500;
  cursor: pointer;
  z-index: 10000 !important;
}

.hero-banner-container h3 {
  font-size: 4rem;
  margin-top: 18px;
  /* color: red; */
  /* text-shadow: 0px 0px 5px rgba(0, 0, 0, 1); */
  background-image: url("https://cdn.cbeditz.com/cbeditz/preview/pink-blue-mix-gradient-background-wallpapers-69-11614501749abuyacrv9a.jpg");
  background-size: cover;
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
}
.hero-banner-container h1 {
  background: linear-gradient(to right, rgba(0, 168, 253), #fff 35%, #e00a95);
  background-size: 200% auto;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: gradient 3s linear infinite;
  font-size: 6em;
  margin-left: -10px;
  margin-top: 18px;
  text-transform: uppercase;
}

.carousel-image img {
  width: 100% !important;
  height: 76vh !important; /* Adjust the height according to your design */
  object-fit: cover;
  object-position: top;
  /* object-position: center;  */
}

/* Feature List Starts From Here  */

.f-list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 50px 0;
}

.f-box {
  width: 180px;
  text-align: center;
  padding: 25px 28px;
  /* box-shadow: 20px 20px 34px rgba(0, 0, 0, 0.1); */
  border: 1px solid #cce7d0;
  border-radius: 4px;
  margin: 15px 0;
  transition: 0.6s ease;
}

.f-box:hover {
  box-shadow: 10px 10px 54px rgba(72, 62, 221, 0.3);
}

.f-box img {
  width: 100%;
  margin-bottom: 16px;
}

.f-box h6 {
  display: inline-block;
  padding: 9px 12px;
  line-height: 1;
  border-radius: 4px;
  color: #088178;
  background-color: #e7ceb4;
}

.f-box:nth-child(2) h6 {
  background-color: #cdebb0;
}

.f-box:nth-child(3) h6 {
  background-color: #c8e5fc;
}

.f-box:nth-child(4) h6 {
  background-color: #cdd4f8;
}

.f-box:nth-child(5) h6 {
  background-color: #e7cea9;
}

.f-box:nth-child(6) h6 {
  background-color: #f9eba7;
}
.marquee-container {
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: black;
  overflow: hidden;
}

.marquee-content {
  white-space: nowrap;
  animation: marquee-animation 20s linear infinite;
}

.marquee-content p,
.dot {
  display: inline-block;
  color: white;
  font-size: 20px;
  margin: 0 50px;
}

.dot {
  width: 15px;
  height: 15px;
  background-color: white;
  border-radius: 50%;
  margin-right: 10px; /* Adjust spacing between dot and text */
}

.marquee-gap {
  /* Hide the duplicated elements */
  position: absolute;
  left: 100%; /* Move the gap to the right of the visible content */
}

@keyframes marquee-animation {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(
      -50%
    ); /* Adjust the percentage based on the number of repetitions */
  }
}

.products-heading {
  text-align: center;
  margin: 50px 0px 0px 0px;
  color: #324d67;
}
.products-heading h2 {
  font-size: 40px;
  font-weight: 800;
}
.products-heading p {
  font-size: 16px;
  font-weight: 200;
}

.loader-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.8); /* White with some transparency */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999; /* Ensure the loader appears on top of everything */
}

.loader {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2); /* Add a subtle shadow */
}

