@import url("https://fonts.googleapis.com/css2?family=Playfair+Display&family=Poppins:wght@300;400;500;600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@400;700&display=swap");

html,
body,
* {
  padding: 0;
  margin: 0;
  font-family: "Montserrat", sans-serif;
  box-sizing: border-box;
}
::-webkit-scrollbar {
  width: 0px;
}

a {
  color: inherit;
  text-decoration: none;
}
.section-p1 {
  padding: 40px 80px;
}
.section-m1 {
  margin: 40px 0;
}
.app__center {
  text-align: center;
}
.app__flex {
  text-align: center;
  align-items: center;
  justify-content: space-between;
  display: flex;
}

.anouncement-container {
  display: flex;
  height: 40px;
  background-color: #000000;
  align-items: center;
  justify-content: space-between;
}
.anouncement-container p {
  font-size: 13px !important;
  font-weight: 600 !important;
  color: #9f9f9f !important;
  padding: 0px 50px;
  color: white;
  word-spacing: 2px;
}

.navbar-container {
  background-color: white;
  border-bottom: 1px solid #000;
  color: #fff;
  padding: 0 20px;
  width: 100%;
}
@media screen and (max-width: 768px) {

  .navbar-container {
    display: none;
  }
}


.app__header {
  align-items: center;
  display: flex;
  height: 50px;
  justify-content: space-between;
  /* margin: 0 120px; */
  max-width: 1450px;
  color: #000000;
}

.app__header .left {
  display: flex;
  list-style: none;
  align-items: center;
  justify-content: center;
  /* display: none; */
  /* gap: 25px; */
}

.app__header .center {
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  position: relative; /* Add position relative to the container */
}

.center a.active {
  position: relative; /* Add position relative to the link */
  color: #ff0000; /* Change the color of the active link */
}

.center a.active::after {
  content: ""; /* Add an empty content for the pseudo-element */
  position: absolute;
  bottom: -5px; /* Adjust the value to control the distance of the underline from the text */
  left: 0;
  width: 100%;
  border-bottom: 2px solid #ff0000; /* Add an underline using border-bottom */
}

.app__header .center li {
  display: flex;
  position: relative;

  align-items: center;
  justify-content: center;
  text-decoration: none;
  list-style: none;
  padding: 0px 15px;

  text-transform: uppercase;
  font-family: "Montserrat", sans-serif;

  font-weight: 500;
  font-size: 14px;
}

.app__header li:hover::after {
  width: 50%;
  border-color: #ff0000; /* Change the color on hover */
}

.app__header li::after {
  content: ""; /* Add an empty content for the pseudo-element */
  position: absolute;
  bottom: -5px; /* Adjust the value to control the distance of the underline from the text */
  left: 12px;
  width: 100%;
  border-bottom: 2px solid transparent; /* Initially transparent */
  transition: border-color 0.3s ease; /* Add transition for smooth effect */
}

.app__header .right {
  display: flex !important;
  list-style: none;
  align-items: center;
  justify-content: space-between;
}

.app__header .right .heart {
  display: flex;
  align-items: center;
  justify-content: center;
  border-left: 1px solid black;
  border-right: 1px solid black;
  padding: 15px 10px;
  margin: 0px 15px;
}


.logo {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid black;
  background-color: white;
  height: 60px;
}
.logo img {
  width: 180px;
  height: 40px;
}

.search {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-size: 14px;
  font-weight: lighter;
  color: #000;
  text-transform: uppercase;
  font-family: "Montserrat", sans-serif;
}

.app__header-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 23px;
  font-weight: lighter !important;
  color: black;
  cursor: pointer;
  /* position: relative; */
  transition: transform 0.4s ease;
  border: none;
  background-color: transparent;
  margin: 0px 15px;
}

.icon-hover:hover {
  transform: scale(1.1, 1.1);
  transition: transform 0.4s ease;
}
.app__header .user {
  display: flex;
  font-weight: lighter !important;
  text-transform: capitalize;
  align-items: center;
  justify-content: center;
  border-left: 1px solid black;
  border-right: 1px solid black;
  padding: 12px 10px;
}

.app__header .user a {
  /* font-weight: 600; */
  font-family: "Montserrat", sans-serif;

  font-weight: 500;
  font-size: 25px;
}

.userName {
  display: flex;
  text-transform: capitalize;
  align-items: center;
  justify-content: center;
  border-left: 1px solid black;
  border-right: 1px solid black;
  padding: 8px 10px;
  text-transform: uppercase;
  font-family: "Montserrat", sans-serif;

  font-weight: 500;
  font-size: 15px;
}
.user-icon {
  font-size: 22px;
}

.search-icon {
  cursor: pointer;
  margin: 0 0 0 15px;
}

.search-icon:hover {
  transform: scale(1.1, 1.1);
}

.cart {
  margin: 0 0 0 15px;
}

.cart-icon {
  display: flex;
  font-size: 22px;
  color: black;
  cursor: pointer;
  top: 1px;
  position: relative;
  transition: transform 0.4s ease;
  border: none;
  background-color: transparent;
}
.cart-icon:hover {
  transform: scale(1.1, 1.1);
}
.cart-item-qty {
  position: absolute;
  top: 110px;
  right: 55px;
  font-size: 12px;
  color: #eee;
  background-color: #f02d34;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  text-align: center;
  font-weight: 600;
}


.wishlist-item-qty {
  position: relative;
  right: 35px;
  top: -6px;
  font-size: 12px;
  color: #eee;
  background-color: #f02d34;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  text-align: center;
  font-weight: 600;
}

